.form-control{position:relative;margin:8px 0;border:1px solid var(--color-border);border-radius:var(--border-radius-small)}.input{display:block;box-sizing:border-box;width:100%;height:50px;padding:0 12px 0 15px;color:var(--color-text);border-radius:var(--border-radius-small);border:none;font-size:16px;font-family:var(--font-family);transition:border-color 0.15s ease;-webkit-appearance:none;background:transparent}.input[disabled]{background-color:rgba(239,239,239,0.3);color:#545454}.input-withFloatLabel{padding-top:10px}.block{display:block;width:100%}.floatLabel{position:absolute;top:0;bottom:0;left:15px;display:block;overflow:hidden;width:calc(100% - 15px);height:22px;margin:auto;color:var(--color-darkGrey);white-space:nowrap;font-size:16px;line-height:22px;transition-timing-function:ease-in-out;transition-duration:0.15s;transition-property:top, font-size;transform:translateZ(0);pointer-events:none;will-change:top, font-size}.floatLabel-floated{top:-25px;font-size:13px}
