.button{position:relative;border:none;font-family:var(--font-family);font-size:16px;font-weight:500;line-height:1.4;border-radius:var(--border-radius);cursor:pointer;display:flex;justify-content:center;align-items:center;outline:none;padding:0 20px;height:50px}.button[disabled]{opacity:0.65;cursor:default}.icon{flex-shrink:0;display:inline-flex;justify-content:center;align-items:center}.icon-withMargin{margin-right:0.5rem}.design-primary{color:white;background-color:var(--color-brand)}.design-primary:hover:not([disabled]){background-color:var(--color-brand-hover)}.design-primary:active:not([disabled]){background:var(--color-brand-active)}.design-primary[disabled],.design-primary[disabled]:hover{background:var(--color-brand)}.design-secondary{background:var(--light-blue);color:var(--color-text);font-weight:600;background-color:transparent;box-shadow:rgba(0,0,0,0.15) 0px 0px 0px 1px inset}.design-secondary:hover:not([disabled]){background:var(--light-blue-hover)}.design-secondary:active:not([disabled]){background:var(--light-blue-active)}.design-secondary[disabled],.design-secondary[disabled]:hover{background:var(--color-border)}.design-light{background:var(--color-grey);color:var(--color-text);font-weight:600}.design-light:hover{background:var(--color-lightGrey)}.design-light:active{background:var(--medium-grey)}.design-light[disabled],.design-light[disabled]:hover{background:var(--color-border)}.design-facebook{background:var(--color-facebook);color:#fff}.design-facebook:hover:not([disabled]){background-color:var(--color-fb-hover)}.design-facebook:active:not([disabled]){background-color:var(--color-fb-active)}.design-yandex{background:var(--color-yandex);color:#000}.design-yandex:hover:not([disabled]){background-color:var(--color-yandex-hover)}.design-yandex:active:not([disabled]){background-color:var(--color-yandex-active)}.design-vk{background-color:var(--color-vk);color:#fff}.design-vk:hover:not([disabled]){background-color:var(--color-vk-hover)}.design-vk:active:not([disabled]){background-color:var(--color-vk-active)}.size-small{height:40px;font-weight:400}.size-default{padding:11px 20px}.size-large{font-size:16px;padding:12px 24px}.block{width:100%}.fit{padding-right:0;padding-left:0}
