.portal{box-shadow:0px 10px 40px rgba(0,0,0,0.25);opacity:0;visibility:hidden;transform:translate3d(0, 30px, 0);display:flex;justify-content:center;flex-direction:column;background:#fff;border-radius:var(--border-radius);padding:10px 0;max-width:450px;font-family:var(--font-family)}.portal:before{border:solid white;border-width:9px 9px 0 0;transform:rotate(-45deg);display:inline-block;padding:3px;background:#fff;content:'';position:absolute;top:-8px}.animated{transition:transform 0.15s, opacity 0.15s, visibility 0.15s}.opened{transform:translate3d(0, 20px, 0);opacity:1;visibility:visible}.horizontal-left:before{left:50px}.horizontal-right:before{right:50px}
