.item{padding:10px 20px;display:flex;justify-content:flex-start;align-items:center;position:relative;cursor:pointer;color:#000;-webkit-user-select:none;-ms-user-select:none;user-select:none}.item svg{flex-shrink:0;margin-right:10px}.rounded{border-radius:var(--border-radius)}.design-light svg{color:var(--color-darkGrey)}.design-light:hover{background:var(--color-lightGrey)}.design-light:active{background:var(--medium-grey)}.design-light.current{background:var(--medium-grey)}.design-brand:hover{color:#fff;background:var(--color-brand)}.design-brand:active{color:#fff;background:var(--color-brand-active)}.disabled{color:var(--color-darkGrey);cursor:default}.disabled svg{color:var(--medium-grey)}.disabled:hover{color:var(--color-darkGrey);background:white}.disabled:active{color:var(--color-darkGrey);background:white}.divided{margin-bottom:10px}.divided:after{content:'';display:block;position:absolute;width:100%;bottom:-10px;left:0;border-bottom:1px solid var(--medium-grey)}.divided+.item{margin-top:20px}
