.label{display:flex;justify-content:flex-start;align-items:flex-start;cursor:pointer}.checker{width:22px;height:22px;background-size:100% 100%;content:'';margin-right:8px;transition:background-image 0.3s;speak:none;border-radius:50%}.design-brand{background-image:url(data:image/svg+xml;base64,)}.input{position:absolute;opacity:0.01;width:0.01px;height:0.01px}.input[disabled] ~ .content,.input[disabled] ~ .checker{cursor:default;color:var(--medium-grey)}.input:checked+.checker.design-brand{background-image:url(data:image/svg+xml;base64,)}.content{line-height:1.4}
